import HttpClient from './http-client'
import store from '../store'
import { GET_ALL_THEME, APPLY_THEME, MODERATION_API_GET_DETAILS, CHANGE_THEME } from './api'
import { DISPATCH_GET_ALL_THEME,  CHANGE_THEME_ID, GET_APP_DATA, IS_THEMES_CHANGE, LIVE_PREVIEW_LOADER } from '../reducer/keys'
 import { CHANGE_WEBSITE_WALL } from './websiteAction'

export const GET_THEME_LIST = () => {
    return (dispatch) => {
        new HttpClient().get(GET_ALL_THEME).then((responce) => {
            const { responseData } = responce.data
            dispatch({
                type: DISPATCH_GET_ALL_THEME,
                payload: responseData,
            });
        }).catch((error) => {
            console.log("ERROR", error)
        })
    }
}

export const GET_WALL_DETAILS = () => {

    var wall_id = localStorage.getItem('wallId') ? localStorage.getItem('wallId') : '';

    return (dispatch) => {
        new HttpClient().post(`${MODERATION_API_GET_DETAILS}`, {
            ownerId: localStorage.getItem('ownerId'),
            wallId: wall_id
        }).then((detailReponse) => {
            const { wall } = detailReponse.data;
        }).catch((error) => {
            console.log("ERROR", error)
        })
    }
}




export const APPLY_THEME_ACTION = (id) => {
    
    const { wall } = store.getState().appData;

    const personalizationId = wall?.ThemeStyle?.id;

    const formData = {
        filterEnableStatus: 0,
        filterStatus: 1,
        padding: wall?.CardStyle?.style?.padding ?? "",
        popupSlideShow: 0,
        popupSlideShowStatus: 0,
        postFeatured: wall?.ThemeStyle?.modal?.action ?? "",
        postFeaturedStatus: 0,
        showReviewHeader: wall?.Reviews?.headerStatus ?? false,
        showReviewList: wall?.Reviews?.themeStatus ?? false,
        textDecorate: wall?.CardStyle?.style?.textDecoration ?? "",
        textDecorateStatus: 0,
        theme: id,
        themeHeaderId: wall?.Reviews?.headerType ?? "",
        themeType: wall?.ThemeInfo?.themeType ?? "",
        themeTypePre: wall?.ThemeInfo?.themeType ?? "",
        trimContentStatus: wall?.CardStyle?.content?.status ?? 0,
        trimcontent: wall?.CardStyle?.content?.trimcontent ?? ""
    };

    return (dispatch) => {
        new HttpClient().post(`${APPLY_THEME}/${localStorage.getItem('wallId')}/${personalizationId}/${localStorage.getItem('website_id')}`, formData).then((response) => {
            const { responseCode } = response.data;
            if (responseCode == 200) {
                dispatch({ type: GET_APP_DATA, payload: store.getState().appData, heightEvent: null });
                setTimeout(() => {
                    dispatch({ type: CHANGE_THEME_ID, payload: id });
                }, 500);
            }
        }).catch((error) => {
            console.log("ERROR", error)
        });

    }
}



export const CHANGE_THEME_ACTION = (id, type, wallKey) => {

    return (dispatch) => {
        dispatch({ type: LIVE_PREVIEW_LOADER, payload: true });
       // const wallId = localStorage.getItem('active_wall') ? localStorage.getItem('active_wall') : localStorage.getItem('wallId')
        new HttpClient().get(`${CHANGE_THEME}/${localStorage.getItem('website_id')}/${id}`).then((response) => {
            const { responseData, responseCode } = response.data;
            if (responseCode == 200) {
              //  if (responseData?.wall?.Personalization) responseData.wall.Personalization["themeType"] = type;
                dispatch(CHANGE_WEBSITE_WALL(localStorage.getItem('active_wall'), responseData.ThemeStyle?.totalPosts ?? 20 , responseData.ThemeInfo?.id));
                dispatch({ type: GET_APP_DATA, payload: {wall : responseData}, heightEvent: null });
            
                setTimeout(() => {
                    dispatch({ type: CHANGE_THEME_ID, payload: id });
                    dispatch({ type: IS_THEMES_CHANGE, payload: true });
                }, 200);
                setTimeout(() => {
                    dispatch({ type: LIVE_PREVIEW_LOADER, payload: false });
                }, 2000);
            }
        }).catch((error) => {
            dispatch({ type: LIVE_PREVIEW_LOADER, payload: false });
            console.log("ERROR", error)
        });

    }
}