import React, { PureComponent } from "react";
import { baseUrl } from "../../../../../Assets/AssetsHelper";

export default class ThemeError extends PureComponent {
  render() {
    const { appData } = this.props;
    const themeId = appData?.wall?.ThemeInfo?.id;
    const postNumber = appData?.post_data?.length || appData?.custom_post?.length || 0;

    const themeConditions = {
      16: appData?.wall?.ThemeStyle?.row,
      53: appData?.wall?.ThemeStyle?.column?.desktop,
      4:  appData?.wall?.ThemeStyle?.bestFit && appData?.wall?.ThemeStyle?.column?.desktop,
      50: 3,
      61: 1,
      3: 1,
      19: 1,
      47: 1,
      55: 1,
    };

    const minNumberPost = themeConditions[themeId] || 1;
    const errorShow = themeConditions[themeId] && postNumber < minNumberPost;

    const ErrorImage = { backgroundImage: `url(${baseUrl('media/images/error/nopost.png')})` };
    const LogoImage = { backgroundImage: `url(${baseUrl('media/images/logo/tagshop-logo.svg')})` };

    return errorShow ? (
      <div className="ts_error_page">
        <div className="ts_error_page_bg" style={ErrorImage}></div>
        <div className="ts_error_page_wrapper">
          <div className="ts_error_content">
            <div className="ts_error_title">Error: Theme Conditions Not Met</div>
            {themeId !== 61 && (
              <div className="ts_error_des">
                Your shoppable gallery isn't visible. You need at least "{minNumberPost}" public posts to see the preview as per the theme rule.
              </div>
            )}
            <div className="ts_error_des">
              <strong>Note: </strong>
              {themeId === 61
                ? "Please make sure your public posts are in reels/videos & image format as this theme does not support posts in text format."
                : "Please make sure your public posts are in image & video format as this theme does not support posts in text format."}
            </div>
          </div>
          <div className="ts_error_page_logo">
            <span className="ts_error_page_logo_ico" style={LogoImage}></span>
          </div>
        </div>
      </div>
    ) : null;
  }
}
