import { Component } from "react";
import ErrorFound from "../../../../ErrorFound";
import InheritSwitch from "./Element/InheritSwitch";
import SelectFont from "../Components/SelectFont";
import { Form } from "react-bootstrap";
import ColorPicker from "../../CommonComponents/ColorPicker";
import RangeInput from "../../CommonComponents/RangeInput";
import { connect } from "react-redux";
import { HDR_BTN_COLOR, HDR_BTN_FONT, HDR_BTN_FONT_COLOR, HDR_BTN_FONT_SIZE, HDR_BTN_TEXT, HDR_BTN_VARIANT, HDR_COLOR, HDR_FONT, HDR_FONT_SIZE, HDR_STAR_COLOR, HDR_TEXT, HDR_VARIANT } from "../../../../../reducer/keys";
import { onSiteUpload } from "../../../../../actions/customization";

class HeaderStyle extends Component{

    onChangeVariant = (e,val) => {
        const {hdrVariant, btnVariant} = this.props
        if(val == 'btn') btnVariant(e.label.key)
        else hdrVariant(e.label.key)
    }


    componentDidUpdate(prevProps) {
        if (this.props.save_and_publish === true && prevProps.save_and_publish !== true) {
            if (this.props.isHeaderUpdate) {
            setTimeout(() => {
                this.onSubmitData();
            }, 500);
            }
        }
    }

    onSubmitData = (e) => {

        const { wallData } = this.props;
        const { SnapUp, ThemeInfo, Reviews } = wallData;
        const { QrCode } = SnapUp;
    
        const replacePlus = (font) => font.includes('+') ? font.replace('+', ' ') : font;
    
        const data = {
            headerFont: replacePlus(Reviews.title.style.font),
            headerFontLink: QrCode.style.font,
            headerFontVariant: Reviews.title.style.variant,
            headerFontColor: Reviews.title.style.color,
            headingText: Reviews.title.text,
            headerFontSize: Reviews.title.style.size,
            buttonFontFamily: replacePlus(Reviews.button.style.font),
            buttonFontLink: Reviews.button.style.font,
            buttonFontWeight: Reviews.button.style.variant,
            buttonTextColor: Reviews.button.style.color,
            buttonColor: Reviews.button.style.background,
            buttonText: Reviews.button.text,
            buttonFontSize: Reviews.button.size,
            ratingColor: Reviews.star.color,
            wallId: ThemeInfo.wallId || localStorage.getItem('active_wall'),
            themeId: ThemeInfo.id,
            websiteId: ThemeInfo.webId || localStorage.getItem('website_id'),
            id: wallData.ThemeStyle.id
        };
    
        onSiteUpload(data, true)
            .then(() => this.setState({ spinner: false }))
            .catch(console.warn);
    };
    

    onFontChange = (event, val) => {
        const { allFont, wallData, hdrFont, btnFont } = this.props
        const { Reviews } = wallData
        const selectedFont = allFont.find(font => font.family == event.label.key)
        const variant = val == 'btn' ? Reviews.button.style.variant : Reviews.title.style.variant
        let selectVariant = JSON.parse(selectedFont.variants).find(item => item == variant)
        if(val == 'btn') btnFont(selectedFont.family, selectVariant ? selectVariant : JSON.parse(selectedFont.variants)[0])
        else hdrFont(selectedFont.family, selectVariant ? selectVariant : JSON.parse(selectedFont.variants)[0])
    }

    render(){
        const { inheritStyle, wallData } = this.props;
        const { Reviews} = wallData
        return <>
            <ErrorFound><InheritSwitch/></ErrorFound>
            <div>
                <p className="h5">Heading</p>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="h_t">Header Text</Form.Label>
                <Form.Control id="h_t" value={Reviews?.title.text} maxLength="30" onChange={e => { this.props.hdrText(e.target.value) }} />
            </div>
            <div className="mt-4">
                <SelectFont inheritStyle={inheritStyle} fontChange={this.onFontChange} variantChange={this.onChangeVariant} fontval={Reviews?.title.style.font} vrntVal={Reviews?.title.style.variant} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="h_c">Font Color</Form.Label>
                <ColorPicker id="h_c" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={Reviews?.title.style.color} changeColor={(clr) => this.props.hdrColor(clr)} inheritStyle={inheritStyle} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="h_s">Font Size</Form.Label>
                <RangeInput inheritStyle={inheritStyle} id="h_s" min={10} max={50} step={1} defaultValue={Reviews?.title.style.size} value={Reviews?.title.style.size} onChange={(event) => { this.props.hdrSize(event) }} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_c">Star Color</Form.Label>
                <ColorPicker id="s_c" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={Reviews?.star.color} changeColor={(clr) => this.props.hdrStar(clr)} inheritStyle={inheritStyle} />
            </div>
            <hr className=""></hr>
            <div>
                <p className="h5">Review Button</p>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_t">Button Text</Form.Label>
                <Form.Control id="b_t" value={Reviews?.button.text} maxLength="30" onChange={e => { this.props.btnText(e.target.value) }} />
            </div>
            <div className="mt-4">
                <SelectFont inheritStyle={inheritStyle} fontChange={event=>this.onFontChange(event, 'btn')} variantChange={event=> this.onChangeVariant(event, 'btn')} fontval={Reviews?.button.style.font} vrntVal={Reviews?.button.style.variant} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_c">Font Color</Form.Label>
                <ColorPicker id="b_c" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={Reviews?.button.style.color} changeColor={(clr) => this.props.btnFontColor(clr)} inheritStyle={inheritStyle} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_bgc">Background Color</Form.Label>
                <ColorPicker id="b_bgc" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={Reviews?.button.style.background} changeColor={(clr) => this.props.btnColor(clr)} inheritStyle={inheritStyle} />
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="b_s">Font Size</Form.Label>
                <RangeInput inheritStyle={inheritStyle} id="b_s" min={10} max={50} step={1} defaultValue={Reviews?.button.size} value={Reviews?.button.size} onChange={(event) => { this.props.btnSize(event) }} />
            </div>
            

        </>
    }
}

const mapStateToProps = (state) => {
    return {
        allFont: state.allFont?.allFonts,
        isHeaderUpdate: state.appData?.isHeaderUpdate,
        inheritStyle: state.appData?.wall?.CardStyle?.inheritStyles,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        hdrFont: (font, variant) => dispatch({type:HDR_FONT, font:font, variant:variant}),
        hdrVariant: (val) => dispatch({type:HDR_VARIANT, payload:val}),
        hdrColor: (val) => dispatch({type:HDR_COLOR, payload:val}),
        hdrSize: (val) => dispatch({type:HDR_FONT_SIZE, payload:val}),
        hdrText:(val) => dispatch({type:HDR_TEXT, payload:val}),
        hdrStar:(val)=>dispatch({type:HDR_STAR_COLOR, payload:val}),
        btnText:(val) => dispatch({type:HDR_BTN_TEXT, payload:val}),
        btnFont: (font, variant) => dispatch({type:HDR_BTN_FONT, font:font, variant:variant}),
        btnVariant: (val) => dispatch({type:HDR_BTN_VARIANT, payload:val}),
        btnFontColor: (val) => dispatch({type:HDR_BTN_FONT_COLOR, payload:val}),
        btnColor: (val) => dispatch({type:HDR_BTN_COLOR, payload:val}),
        btnSize: (val) => dispatch({type:HDR_BTN_FONT_SIZE, payload:val}),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(HeaderStyle)